body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  border: solid 10px gray;
  padding: 10px;
  background-color: #f5f8fd;
}

.capacitate {
  border: solid 10px gray;
  padding: 10px;
  background-color: #f5f8fd;
}

ul {
  font-size: 0.9rem;
  width: 100%;
  margin-left: 10px;
  list-style-type: none;
}

.resume {
  color: black;
}

.toolbar {
  display: flex;
  justify-content: flex-end;
  margin-right: 10px;
}

.button-layout {
  background-color: white;
  padding: 5px;
  cursor: pointer; 
  border-radius: 5%;
  border: solid 2px #E5E5E5; 
  box-shadow: 2px 2px 5px 0px #00000073;
  transition: all ease 0.3s;
}

.button-layout:active {
  box-shadow: 1px 1px 1px 0px gray
}

.avatar {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.avatar-icon {
  color: black;
}

.avatar-photo img {
  vertical-align: middle;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: solid 2px #E5E5E5;
  box-shadow: 2px 2px 5px 0px #00000073;
}

.avatar-information {
  margin-left: 20px;
}

.avatar-information-name {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.avatar-information-phone {
  font-size: 1.3rem;
}

.avatar-information-email {
  color: #5887F5;
  font-size: 1.3rem;
}

.avatar-information-github {
  color: #5887F5;
  font-size: 1.3rem;
}

.avatar-information-quote {
  font-size: 1.1rem;
  margin-top: 10px;
  font-style: italic;
}

.avatar-study {
  font-size: 1.3rem;
  margin-left: 40px;
}

.avatar-study-title {
  font-weight: bold;
}

.avatar-study-idioms {
  font-size: 1.3rem;
  font-weight: bold;
  margin-top: 20px;
}

.not-found-container {
  border: solid 10px gray;
  padding: 10px;
  background-color: #f5f8fd;
  height: 100vh;
}

.not-found {
  text-align: center;
  margin-top: 50px;
}

.otros-container {
  border: solid 10px gray;
  padding: 10px;
  background-color: #f5f8fd;
  text-align: center;
}

.otros-imagen img {
  vertical-align: middle;
  width: 50%;
  height: 50%;
  border: solid 2px #E5E5E5;
  box-shadow: 2px 2px 5px 0px #00000073;
  margin: 10px;
}

.otros-imagen img:hover {
  box-shadow: 4px 4px 10px 0px #00000073;
}

.favoritos-container {
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
}

.favoritos {
  margin-left: 10px;  
  margin-right: 10px;  
  background-color:#44c767;
	border-radius:10px;
	border:1px solid #18ab29;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	padding:6px 31px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2f6627;
}
.favoritos:hover {
	background-color:#5cbf2a;
}
.favoritos:active {
	position:relative;
	top:1px;
}